import React, {useState} from "react";
import { useDropzone } from 'react-dropzone';
import axios from "axios";

import HTMLEditorBallonBlock from "./HTMLEditorBallonBlock";

import Card from "react-bootstrap/Card";
import Alert from "react-bootstrap/Alert";

// import Modal from "react-bootstrap/Modal";
// import Button from "react-bootstrap/Button";
// import InputGroup from "react-bootstrap/InputGroup";
// import FormControl from "react-bootstrap/FormControl";
// import Form from "react-bootstrap/Form";


export default function CaseWithCloudinary() {
    const [imageUploading, setImageUploading] = useState(false);
    const [cloudinaryURLs, setCloudinaryURLs] = useState([]);
    const [htmlfeedback, setHtmlfeedback] = useState('');
    const [uploadError, setUploadError] = useState(null);

    const onDrop = React.useCallback(acceptedFiles => {
        setImageUploading(true);

        const uploaders = acceptedFiles.map(file => {
            const formData = new FormData();
            formData.append("file", file);
            formData.append(
                "tags",
                `2020, econolabs`
            );
            formData.append("upload_preset", 'qzvjwavu'); // Replace the preset name with your own 
            formData.append("api_key", '521328645139184'); // Replace API key with your own Cloudinary key
            formData.append("api_secret", 'HDYNOu1ysDutpL7qOWovQqi1z9o');
            formData.append("timestamp", (Date.now() / 1000) | 0);
            return axios
                .post(
                    "https://api.cloudinary.com/v1_1/econolabs/image/upload",
                    formData,
                    {
                        headers: { "X-Requested-With": "XMLHttpRequest" }
                    }
                )
                .then(response => {
                    setCloudinaryURLs(cloudinaryURLs => [...cloudinaryURLs, response.data.url])
                    //    let url = response.data.url;
                })
                .catch(err => {
                    setUploadError(err)
                    //console.log(err);
                });
        });
        axios.all(uploaders).then(() => {
            setImageUploading(false);
        });


        // Do something with the files
    }, [])
    const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop })

    return <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
        <Card.Header>  </Card.Header>
        <Card.Body>
            <Card.Title>  </Card.Title>
            {/* <Card.Text></Card.Text> */}
            <HTMLEditorBallonBlock htmlfeedback={htmlfeedback} setHtmlfeedback={setHtmlfeedback}/>
            <hr />
            <Alert variant="secondary" className="text-center mt-5" {...getRootProps()}>
                <input {...getInputProps()} />
                {
                    isDragActive ?
                        "  ..." :
                        "  ()"
                }
            </Alert>

            {!!uploadError ? <Alert variant="danger">{uploadError}</Alert> : null}
            {!!cloudinaryURLs && !!cloudinaryURLs.length > 0 && !imageUploading ?
                <div>
                    {cloudinaryURLs.map(url => <img key={url} src={url} className="img-fluid" alt="trading"></img>)}
                </div>

                : null}

        </Card.Body>
    </Card>
}

